/**
 * HELLO! I take my work seriously, but not my comments, as they are the one place I can express myself whilst being informative.
 * I am my comments, it's how I am, it's who I am. Life is too short for formality. Although, I probably tip the scales too far in the other direction, sorry.
 * This Component takes an SVG file, and converts it to a modifyable... modifiable? (I'll google that later) component tree.
 * This means you can upload your (or someone elses) SVG and click on the white areas to colour it right away.
 * There may be better ways of doing this, but so far this is my way, and I try my best.
 * @returns NAFFINK... uh wait let me check.
 */
function SVGHolder(props) {

  /**
   * Some naughty global variables, will fix later probably, maybe, well... We'll see.
   */
  let parsedElementsArray = [];
  let pathID = 0;
  
  /**
   * File related Constants / Hooks
   */
  const [selectedFile, setSelectedFile] = useState();
  const [selectedFileExtension, setSelectedFileExtension] = useState();
  const [testJPG, setTestJPG] = useState("https://image.smythstoys.com/original/desktop/193849.jpg");

  const [svgData, setSvgData] = useState( {
    key:uuidv4(),
    width: 0,
    height: 0,
    data: []
  })

  /**
   * Fill functionality related vars / hooks
   */
  const basefill="#FFFFFF";
  //const outlineFill="#000000";
  const [fillColors, setFillColors] = useState([]);


  /**
   * This function is used to set up the initial array of fill colors dynamically
   * @param {*} fillColor 
   */
  function updateInitialFillColorArray(fillColor) {
    setFillColors((prevData) => {
      return [...prevData, fillColor];
    });
  }


  /**
   * This is our colouring function. It just updates the fill colour in the fill array
   * @param {*} e 
   */
  function setFillColor(elementID) {

      const tempColorArray = [...fillColors];
      tempColorArray[elementID] = props.colorPickerValue;
      setFillColors(tempColorArray);
  }


  /**
   * This is our colouring function. It just updates the fill colour in the fill array
   * 
   */
   function resetColors() {
     setFillColors([])
    }

  //MOVE ALL THIS TO A HELPER JS AND IMPORT later once functioning! Don't forget.

  /**
   * Function to retrieve the coordinates of... something. Our SVG paths I guess? Yes, let's run with that.
   * @param {*} pathElement 
   * @returns 
   */
  function getPathElementCoordinates(pathElement) {

    //Get Pat coordinates from path element abd return them
    let coordinates = pathElement.getAttribute("d");
    return coordinates;
  }


  /**
     * This gets a group element's attributes and checks for children. It's basically like clear-blue.
     * @param {*} groupElement 
     * @returns 
     */
   function getGElementsAndAttributes (groupElement) {

    //Build our base object first - grab the 2 key attributes any <g> element might have: "transform" & "id" if they don't have 'em, they are set to null.
    let groupElementObject = {
      'gTransformAtr' : groupElement.getAttribute("transform") ? groupElement.getAttribute("transform") : null,
      'gIdAtr' : groupElement.getAttribute("id") ? groupElement.getAttribute("id") : null,
      'hasChildren' : groupElement.children ? true : false,
      'groupChildren' : [],
      'pathChildren' : []
    }

    //Next check if there any children elements.
    if (groupElementObject.hasChildren) {

      //If there are, we grab them and put them in a variable. Congratulations, you have kids. lots of kids... You're going to need a bigger boat.
      let gChildren = groupElement.children;

      //Loop through each child.
      for (let i = 0; i < gChildren.length; i++) { 
        if(gChildren[i].tagName === "g") {

          //If child is another <g> element run this function again to add it to a nested array in our object as a new object parameter
          groupElementObject.groupChildren.push(getGElementsAndAttributes(gChildren[i]));
        } else if(gChildren[i].tagName === "path") {

          //If current child is a <path> element add its "d" attribute cordinates to our a new path object object as a new parameter
          let newPath = {
            "pathId" : pathID,
            "pathCoordinates" : getPathElementCoordinates(gChildren[i])
          }  
          //push this to an array on the parent tree g element        
          groupElementObject.pathChildren.push(newPath);
          //Add the fill to the fill color-array need to review this if black lines become an issue
          updateInitialFillColorArray((pathID === 0)? basefill/*outlineFill*/ : basefill);
          //increment the pathID
          pathID++;
        }
      }      
    }
    return groupElementObject;
   }


   /**
    * Calculate the dimensions of the SVG basd on the SVG tagged main element
    * @param {*} svgElement 
    */
   function getSvgDimensions(svgElement) {

    //Get Width and height from their attributes
    //added the regEx to remove any px or pt or anything that might be at the end of the string.
    let svgWidth = svgElement.hasAttribute("width") ? svgElement.getAttribute("width").replace(/[^\d.]/g, '') : null; 
    let svgHeight = svgElement.hasAttribute("height") ? svgElement.getAttribute("height").replace(/[^\d.]/g, '') : null;

    //If either of those failed try checking the viewBox attribute instead
    if (svgWidth === null || svgHeight === null) {

      //assuming the viewBox attribute is of the format "x(0) y(0) W(unknown) H(unknown)")
      //may have to adapt if SVG is not 0,0 x,y registered
      let viewBoxString = svgElement.hasAttribute("viewBox") ? svgElement.getAttribute("viewBox").substring(4) : null;

      //if we have a viewbox proceed to set the values otherwise default to 100% for now (Will add more methods after unit testing SVGs from various sources)
      svgWidth = viewBoxString !== null ? viewBoxString.substr(0,viewBoxString.indexOf(' ')) : "100%";
      svgHeight = viewBoxString !== null ? viewBoxString.substr(viewBoxString.indexOf(' ')+1) : "100%";
    }
    return [svgWidth,svgHeight];
   }


  /**
   * Main parsing function allowing for differently formatted SVGs using paths nested in an unknown number of group parents.
   * @param {*} temporarySvgText 
   */
  function parseTemporarySvg(temporarySvgText) {

    //Reset the color array otherwise if we are loading in our second picture we will be addinf onto the existing array and inheriting those colors.
    resetColors();

    //Create new DOMParser and parse the passed in SVG data
    let parser = new DOMParser();
    let svgXml = parser.parseFromString(temporarySvgText, "text/xml");

    //Get our main SVG element so we can work below it and ignore anythign extraneous
    let svgElement = svgXml.getElementsByTagName("svg")[0];
    let svgDimensions = getSvgDimensions(svgElement);

    //Get all the immediate children below this svg element
    let svgChildGroupElements = svgElement.children;

    //Loop through the elements
    for (let i = 0; i < svgChildGroupElements.length; i++) {

      //push an object containing the attributes we need to reconstruct an element of our SVG as a component (or series of nested components) to our placeholder array.
       let groupElement = getGElementsAndAttributes(svgChildGroupElements[i]);
       parsedElementsArray.push(groupElement);
    }
    return [parsedElementsArray, svgDimensions];
  }

  /**
   * Just an extension checker. Will add second param to confirm what files we are checking for. Move this to helper js file
   * @param {*} ext 
   * @returns 
   */
  function checkFileExtension(ext) {
    switch (ext) {
      case "svg":
        return true;
      case "jpg":
        return true;
      case "png":
        return true;
      case "gif":
        return true;
      case "tiff":
        return true;
      case "bmp":
        return true;
      default:
        return false;
    }
  }

  /**
   * 
   * @param {*} file 
   */
  function invertJPG(file) {

    // Jimp.read(file)
    //   .then(image => {
    //     //console.log(image);
    //     image.name = "image-inverted.jpg";
    //     return image
    //       //.resize(256, 256) // resize
    //       //.quality(100) // set JPEG quality
    //       .greyscale() // set greyscale
    //       .invert()
    //       //.write('image-inverted.jpg'); // save
    //   })
    //     .catch(err => {
    //       console.error(err);
    //   });
  }
  

  /**
   * 
   * @param {*} file 
   */
  function convertRasterToSVG(file) {

    const invertedFile = invertJPG(file);
    setTestJPG(invertedFile);

  }

  /**
   * give us the extension of a file. //move me. Not in the emotiona sense...
   * @param {*} nameString 
   * @returns 
   */
  function getFileExtension(nameString) {
    const fileExtension = nameString.split('.').pop();
    return fileExtension;
  }


  /**
   * Monitor for a file selection and update the states. I like comments. Do you like comments?
   * @param {*} e 
   */
  function changeHandlerImage(e) {

    //Set the file to a state and let us know it's been picked. We could probably live without this second part, but it's handy for some "flooooooow" control maaaan.
    //ACTUALLY YES! I found a use. Hoisting the variable, to control if we show the pallette or not AND checking on click of the submit button. Not 100% sure on the spelling of pallette... Color picker then.
    const fileExtension = getFileExtension(e.target.files[0].name);

    //Let's check someone hasn't cheekily tried to upload soemthing that we do not support.
    if(checkFileExtension(fileExtension)) {

      //If file is an "image" then happy days! We can proceed with this whole sordid affair.
      setSelectedFile(e.target.files[0]);
      setSelectedFileExtension(fileExtension);
      props.setIsFileSelected(true);
      
    } else {
      console.log("TODO - ." + fileExtension + " fIles are not supported, update the input box css or something");
      setSelectedFileExtension("");
    }
  }


  /**
   * On click of submit we need to parse the SVG or if a raster, first make that an appropriate SVG.
   * Don't ask why, if you dodn't know, then you might be looking at the wrong code.
   * @param {*} 
   */
  function handleSubmission() {
    if(props.isFileSelected) {
      props.setSelectedNewFile(true);

      //Instantiate a new FileReader to grab the data from the file once loaded.
      const reader = new FileReader();   

      if(selectedFileExtension === "svg") {
        //Wait for it to load before parsing. Waaaait, waait for it. Hold! HOOOOOOOOLD!
        reader.onload = () => {

          //Parse! Parse! Parse! Get you're probably overcomplicated parsing done here, folks! I'm loosing it. Help.
          let staticSvgData = parseTemporarySvg(reader.result);

          //Set the state with our delicious specific and controllable oven fresh parsed data, and watch the magic happen! Or don't I'm not sure...
          setSvgData((prevData) => {
            return {
              ...prevData,
              key: uuidv4(),
              width: staticSvgData[1][0],
              height: staticSvgData[1][1],
              data: staticSvgData[0]
            };
          });
          props.setSelectedNewFile(false);
          props.setIsFileConfirmed(true);
        }
        //Parse the selected file. Parse... Parse? Parsnips! Did you know they are surprisingly delicious just boiled, like you would a carrot. However, they are best when roasted with some honey. Mmmmmmmmmmmmmm, honey.
        reader.readAsText(selectedFile);

      } else if (selectedFileExtension === "") {
        //If we click before uploading OR have chosen something that is not a supported image file do nothing.

      } else {
        //we must have an image (other than an SVG), so let's set about inverting and converting it.
        reader.onloadend = function() {

          //Here we goooooooooo
          convertRasterToSVG(reader.result);
        }
        //we need this one as data not as text, as we need to mess with it before we can use it.
        reader.readAsDataURL(selectedFile);
      }
      
      props.setIsFileSelected(false);
    } else {

    }
  }


  /**
   * This builds the path elements of our SVG component tree
   * @param {*} svgElementData 
   * @returns 
   */
  function buildSvgPaths(svgElementData) {
    return (
      <PathElem
        key={uuidv4()}
        pathId = {svgElementData.pathId} 
        pathCoordinates = {svgElementData.pathCoordinates}
        setFill = {setFillColor}
        fills = {fillColors}
      />
    )
  }


  /**
   * If you're still reading comments, hello, thank you for checking out my code, would you like to be friends?
   * Oh the fucntion? Ah yeah, it builds the g elements of our SVG component tree, well, it doesn't at the time of writing this,
   * but I'm confident it will by the time I'm finished. Look out! Behind you! *woosh, gone*
   * @param {*} svgElementData 
   * @returns 
   */
  function buildSvgGroups(svgElementData) {
    if(svgElementData.hasChildren) {
      return (
        <GroupElem
          key={uuidv4()}
          id={svgElementData.gIdAtr}
          transform={svgElementData.gTransformAtr}
        >
          {(svgElementData.pathChildren.length === 0) && svgElementData.groupChildren.map(buildSvgGroups)}
          {(svgElementData.pathChildren.length !== 0) && svgElementData.pathChildren.map(buildSvgPaths)}
        </GroupElem>
      );
    }
  }

  /**
   * Return our component, look! We made a lovely svg cyber-baby. Together. We did it together.
   */
  return (

    <Fragment>
      <label htmlFor="svgInput" className="file-upload-input">
      {props.isFileConfirmed? "Select a different File?" : "Upload your own JPG/PNG/BMP/SVG?"}</label>
			<input id="svgInput"  type="file" accept=".jpg,.gif,.png,.tiff,.svg,.bmp" onChange={changeHandlerImage} />

      
      {props.isFileSelected ? (
        <div>
          <p>Filename: {selectedFile.name}</p>
					<p>Filetype: {selectedFile.type}</p>
          <h3>is this the file you wish to upload?</h3>
        </div>
      ) : (
        <br/>
      )}
			<div>
				<button onClick={handleSubmission}>Submit</button>
			</div>
      {!props.selectedNewFile ? (
      <SvgElem width={svgData.width} height={svgData.height}>
        {svgData.data.map(buildSvgGroups)}
      </SvgElem>
      ) : (
        <p></p>
      )}
    </Fragment>
  );
}


//Export(s).
export default SVGHolder;
