import { v4 as uuidv4 } from "uuid";
import ColorSwatch from "./ColorSwatch";

function ColorSwatchGroup (props) {

    return (

        <div className="color-swatch-group">
        {props.colors.map((color, index) => (
            <ColorSwatch key={uuidv4()} /*mouseOver={props.mouseOverFunction} mouseOut={props.mouseOutFunction}*/ click={props.updateColor} color={color} value={color}/>
            ))}
        </div>
    )
}

export default ColorSwatchGroup;