import { useState } from 'react';
import SVGHolder from './SVGHolder';
import ColorPicker from './UI/ColorPicker';
import "./App.css";

function App() {

  const [colorPickerColor, setColorPickerColor] = useState("#ffffff");
  const [isFileSelected, setIsFileSelected] = useState(false);
  const [isFileConfirmed, setIsFileConfirmed] = useState(false);
  const [selectedNewFile, setSelectedNewFile] = useState(false);

  

  function updateColorHandler (newColor) {
    setColorPickerColor(newColor);
  }

  return (
    <div className="App">
      <header className="App-header">
        <SVGHolder colorPickerValue={colorPickerColor} selectedNewFile={selectedNewFile} setSelectedNewFile={setSelectedNewFile} isFileSelected={isFileSelected} setIsFileSelected={setIsFileSelected} setIsFileConfirmed={setIsFileConfirmed} isFileConfirmed={isFileConfirmed}/>
        {isFileConfirmed && (
        <ColorPicker updateColorHandler={updateColorHandler} colorPickerValue={colorPickerColor}/>
        )}
      </header>
    </div>
  );
}

export default App;