import { useState } from "react";
import { v4 as uuidv4 } from "uuid";
import { defaultColorsArray } from "./Colors";
import ColorSwatchGroup from "./ColorSwatchGroup";
import "./ColorPicker.css";

function ColorPicker(props) {

  const [previewColor, setPreviewColor] = useState("#ffffff");
  const [lastpreviewColor, setlastpreviewColor] = useState("#ffffff");
  function updatePreviewColor(e) {
    setPreviewColor(e.target.value);
  }

  function updateColor(e) {
    setPreviewColor(e.target.value);
    setlastpreviewColor(previewColor);
    props.updateColorHandler(e.target.value);
  }

  function revertPreviewColor() {
    console.log("RollOFf: " + lastpreviewColor)
    setPreviewColor(lastpreviewColor);

  }

  return (

    <div className="color-picker">
        <button style={{backgroundColor: previewColor, width: "60px", height: "40px"}}></button><br/><br/>

        {defaultColorsArray.map((colorArray, index) => (
          <ColorSwatchGroup key={uuidv4()} mouseOverFunction={updatePreviewColor} mouseOutFunction={revertPreviewColor} updateColor={updateColor} colors ={colorArray}/>
          ))}
    </div>

    
  
  );
}
export default ColorPicker;
